export default {
    'pageName': 'unpayclearform',
    'pageLable': '',
    'student': true,
    'form': {
        'order_id': '',
        'student_id': '',
        'classes_id': '',
        'times': 0,
        'amount': 0,
        'note': ''
    },
    'created': function (vm) {
        if (vm.$store.state.dialogs.dailogdata) {
            vm.order = vm.$store.state.dialogs.dailogdata
            vm.currStudent = vm.$store.state.envs.currStudent
            vm.localdata.form.order_id = vm.stepsdata.order_id
            vm.localdata.form.classes_id = vm.stepsdata.classes_id
        }
    },
    'formField': [
        {
            'type': 'vmsubtext',
            'label': '',
            'prop': 'order',
            'subprop': 'order_no'
        },
        {
            'type': 'vmsubtext',
            'label': '',
            'prop': 'order',
            'subprop': 'body'
        },
        {
            'type': 'vmsubtext',
            'label': '',
            'prop': 'order',
            'subprop': 'order_amount',
            'text': ''
        },
        {
            'type': 'vmsubtext',
            'label': '',
            'prop': 'order',
            'subprop': 'pay_amount',
            'text': ''
        },
        {
            'type': 'vmsubtext',
            'label': '',
            'prop': 'order',
            'subprop': 'unpay_amount',
            'text': ''
        },
        {
            'type': 'vmsubtext',
            'label': '',
            'prop': 'order',
            'subprop': 'origin_times',
            'text': ''
        },
        {
            'type': 'vmsubtext',
            'label': '',
            'prop': 'order',
            'subprop': 'present_times',
            'text': ''
        },
        {
            'type': 'vmsubtext',
            'label': '',
            'prop': 'order',
            'subprop': 'unit_price',
            'text': ''
        },
        {
            'type': 'numberinput',
            'label': '',
            'text': '',
            'prop': 'times',
            'field': 'times'
        },
        {
            'type': 'numberinput',
            'label': '',
            'text': '',
            'prop': 'amount',
            'field': 'amount',
            'max': function (vm) {
                let maxvalue = vm.order.unpay_amount
                return maxvalue
            },
        },
        {
            'type': 'input',
            'label': '',
            'prop': 'note',
            'field': 'note'
        }
    ],
    'pageTable': 'clear',
    'pageTemplate': 'form',
    'pagePath': '',
    rulesData(vm) {
        return {
            times: [
                { required: true, validator: vm.validateNumberinput, message: '', trigger: 'blur' }
            ],
            amount: [
                { required: true, validator: vm.validateNumberinput, message: '', trigger: 'blur' }
            ],
            note: [
                { required: true, message: '', trigger: 'blur' },
                { min: 1, max: 256, message: ' 1  256', trigger: 'blur' }
            ]
        }
    }
}



