<template>
    <div class="modal-dialog">
        <div class="modal-content">
            <div class="modal-header">
                <button class="close" type="button" @click="lbClosedialog($event)">
                    <span aria-hidden="true"></span>
                    <span class="sr-only"></span>
                </button>
                <h3 class="modal-title">
                    <i class="fa fa-flag"></i>{{title}}
                </h3>
            </div>
            <div class="modal-body">
                <el-form :model="localdata.form" :rules="rules" ref="ruleForm" label-width="100px">
                    <el-collapse value="1">
                        <el-collapse-item title="" name="1">
                            <el-form-item label="">
                                <el-radio-group v-model="localdata.form.lesson_type" @change="handleTypeCommand">
                                    <template v-for="(value,index) in localdata.lesson_type">
                                        <el-radio-button :label="index">{{value.text}}</el-radio-button>
                                    </template>
                                </el-radio-group>
                                <!-- <lb-buttongroup :group-data="localdata.lesson_type" v-model="localdata.form.lesson_type" @input="handleTypeCommand"></lb-buttongroup> -->
                            </el-form-item>
                            <el-form-item label="">
                                <el-cascader placeholder="" :options="getreeData" v-model="localdata.form.cate_array" filterable change-on-select></el-cascader>
                            </el-form-item>
                            <el-form-item label="" prop="lesson_name">
                                <el-input v-model.trim.lazy="localdata.form.lesson_name"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="lesson_no">
                                <el-input v-model="localdata.form.lesson_no"></el-input>
                            </el-form-item>
                            <el-form-item label="" v-if="localdata.form.lesson_type=='0'">
                                <el-input placeholder="" v-model="localdata.form.lesson_days">
                                    <el-select v-model="localdata.form.lesson_days" slot="prepend" placeholder="">
                                        <el-option label="1" value="30"></el-option>
                                        <el-option label="1" value="90"></el-option>
                                        <el-option label="" value="180"></el-option>
                                        <el-option label="1" value="365"></el-option>
                                    </el-select>
                                    <el-button slot="append"></el-button>
                                </el-input>
                            </el-form-item>
                        </el-collapse-item>
                        <el-collapse-item title="" name="2">
                            <el-form-item label="" v-if="this.localdata.form.lesson_type=='0'">
                                <el-radio-group v-model="localdata.form.price_model" @change="handleHoursCommand">
                                    <template v-for="(value,index) in localdata.price_model">
                                        <el-radio-button :label="index">{{value.text}}</el-radio-button>
                                    </template>
                                </el-radio-group>
                            </el-form-item>
                            <el-form-item label="">
                                <lb-numberinput v-model="localdata.form.unit_price" text="/" @change="changeTimePrice" v-if="localdata.form.price_model=='1'"></lb-numberinput>
                                <lb-numberinput v-model="localdata.form.unit_price" text="/" @change="changeTimePrice" v-if="localdata.form.price_model=='0'"></lb-numberinput>
                            </el-form-item>
                            <el-form-item label="">
                                <lb-numberinput v-model="localdata.form.inc_timesprice" text="" @change="changeTimePrice" v-if="localdata.form.price_model=='1'"></lb-numberinput>
                                <lb-numberinput v-model="localdata.form.inc_period" text="" @change="changeTimePrice" v-if="localdata.form.price_model=='0'"></lb-numberinput>
                            </el-form-item>
                            <el-form-item label="">
                                <lb-numberinput v-model="localdata.form.price" text=""></lb-numberinput>
                            </el-form-item>
                        </el-collapse-item>
                        <el-collapse-item title="" name="3">
                            <el-form-item label="" v-if="localdata.form.price_model == '0'">
                                <lb-numberinput v-model="localdata.form.inc_times" text="" @change="changeTimeInc"></lb-numberinput>
                            </el-form-item>
                            <el-form-item label="">
                                <el-input placeholder="" v-model="localdata.form.unit_hours" @change="changeTimeInc">
                                    <el-select v-model="localdata.form.unit_hours" slot="prepend" placeholder="">
                                        <el-option label="45" value="0.75"></el-option>
                                        <el-option label="1" value="1.00"></el-option>
                                        <el-option label="1" value="1.50"></el-option>
                                        <el-option label="2" value="2.00"></el-option>
                                    </el-select>
                                    <el-button slot="append"></el-button>
                                </el-input>
                            </el-form-item>
                            <el-form-item label=":">
                                 <lb-numberinput v-model="localdata.form.inc_hours" text=""></lb-numberinput>
                            </el-form-item>
                        </el-collapse-item>
                    </el-collapse>
                </el-form>
            </div>
            <div class="modal-footer">
                <button class="btn btn-primary" @click="handleClick"></button>
                <button class="btn btn-warning" @click="lbClosedialog($event)"></button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'newLesson',
    data() {
        let localdata = {
            'lesson_type': [{
                'value': '0',
                'text': ''
            }, {
                'value': '1',
                'text': '11'
            }, {
                'value': '2',
                'text': ''
            }],
            'form': {
                'lesson_type': '0',
                'cate_array': [],
                'lesson_name': '',
                'lesson_no': '',
                'lesson_days': 0,
                'price_model': '0',
                'unit_price': 0,
                'inc_times': 0,
                'inc_period': 0,
                'inc_timesprice': 0,
                'price': 0,
                'unit_hours': 0,
                'inc_hours': 0
            },
            'price_model': [{
                'value': '0',
                'text': ''
            }, {
                'value': '1',
                'text': ''
            }]
        }
        return {
            localdata,
            model: 'course',
            title: '',
            rules: {
                lesson_name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
                ],
                lesson_no: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
                ]
            }
        }
    },
    mounted() {
        if (this.$store.state.dialogs.dailogdata&&this.$store.state.dialogs.dailogdata['_id']) {
            this.title = ''
            this.setEditModle(this.$store.state.dialogs.dailogdata['_id'])
            this.localdata.form = this.lodash.assign(this.localdata.form, this.$store.state.dialogs.dailogdata)
        } else {
            this.title = ''
        }
        this.getTableApidata('cate')
    },
    computed: {
        getreeData() {
            let cateData = this.$store.state.models.models.cate.data
            let treeData = []
            let treemap = {}
            for (var item of cateData) {
                treemap[item._id] = {
                    value: item._id,
                    label: item.name
                }
            }
            for (var subitem of cateData) {
                if (subitem.pid == '') {
                    treeData.push(treemap[subitem._id])
                } else {
                    if (typeof treemap[subitem.pid] == 'object') {
                        if (typeof treemap[subitem.pid].children !== 'object') {
                            treemap[subitem.pid].children = []
                        }
                        treemap[subitem.pid].children.push(treemap[subitem._id])
                    }
                }
            }
            return treeData
        }
    },
    watch: {},
    methods: {
        changeTimePrice() {
            if (this.localdata.form.price_model == '1') {
                this.localdata.form.price = this.localdata.form.unit_price * this.localdata.form.inc_timesprice
            } else {
                this.localdata.form.price = this.localdata.form.unit_price * this.localdata.form.inc_period
            }
        },
        changeTimeInc() {
            this.localdata.form.inc_hours = this.localdata.form.unit_hours * this.localdata.form.inc_times
        },
        handleTypeCommand() {
            if (this.localdata.form.lesson_type == '0') {
                this.localdata.form.price_model = '0'
            } else {
                this.localdata.form.price_model = '1'
            }
        },
        handleTimeCommand(value) {
            this.localdata.form.unit_hours = value
            this.changeTimeInc()
        },
        handleHoursCommand() {
            this.changeTimePrice()
        },
        handleCommand(value) {
            this.localdata.form.lesson_days = value
        },
        handleClick() {
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    this.handleSave().then(() => {
                        this.$message({
                            message: '',
                            type: 'success'
                        })
                        this.lbClosedialog()
                        this.$store.state.envs.currDialog = 'lb-lesson'
                    }, (e) => {
                        console.log(e)
                    })
                } else {
                    return false;
                }
            })
        }
    }
}
</script>



