<template>
    <div class="panel panel-default">
        <div class="panel-heading"></div>
        <ul class="list-group">
            <li class="list-group-item clear" v-for="(item, index) in localdata.dict">
                <h6 class="list-group-item-head text-danger" style="font-size:16px;">{{item.text}}
                    <span class="pull-right">
                        <template v-if="getActionOption('systememdictedit')">
                            <a @click="handleShowDialog('dictitemdialog',item)"></a>
                            <!-- lb-dictitems -->
                        </template>
                    </span>
                </h6>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name: 'dict',
    data() {
        let localdata = {
            'dict': [{
                'type': '1',
                'text': ''
            }, {
                'type': '2',
                'text': ''
            }, {
                'type': '3',
                'text': ''
            }, {
                'type': '4',
                'text': ''
            }, {
                'type': '5',
                'text': ''
            }, {
                'type': '6',
                'text': ''
            }, {
                'type': '7',
                'text': ''
            }, {
                'type': '8',
                'text': ''
            },]
        }
        return {
            localdata,
        }
    },
    computed: {},
    watch: {},
    methods: {}
}
</script>



