export default {
    'pageName': 'rolesform',
    'pageLable': '',
    'form': {
        'name': '',
        'desc': '',
    },
    'formField': [
        {
            'type': 'input',
            'label': '',
            'prop': 'name',
            'field': 'name'
        },
        {
            'type': 'input',
            'label': '',
            'prop': '',
            'field': 'desc'
        },
        {
            'type': 'switch',
            'label': '',
            'prop': '',
            'field': 'defrole'
        }
    ],
    'pageTable': 'role',
    'pageTemplate': 'form',
    'pagePath': '',
    rules: {
        name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 256, message: ' 1  256 ', trigger: 'blur' }
        ],
    }
}


