<template>
    <div class="modal-over bg-black" style="z-index: 6000;margin: -100px 0px 0px -100px;">
        <el-dialog :visible.sync="dialogVisible" size="large">
            <span>
                <div class="modal-content">
                    <div class="regcontent">
                        <h1></h1>
                        <p></p>
                        <p> </p>
                        <p>() </p>
                        <p>
                            <strong></strong>
                        </p>
                        <p>1.1 </p>
                        <p>1.2 </p>
                        <p>1.3 &nbsp;</p>
                        <p>
                            <strong></strong>
                        </p>
                        <p>2.1 </p>
                        <p>
                            <strong></strong>
                        </p>
                        <p>3.1  (1) 
                            <br> (2) 
                            <br> (3) 
                            <br> (4) 
                            <br> (5) 
                            <br> (6) 
                            <br> (7) 
                            <br> (8) 
                            <br> (9) 
                            <br> (10) 
                        </p>
                        <p>3.2 
                        </p>
                        <p>3.3 </p>
                        <p>
                            <strong></strong>
                        </p>
                        <p>4.1 </p>
                        <p>4.2 </p>
                        <p>4.3 </p>
                        <p>4.4 </p>
                        <p>4.5</p>
                        <p>
                            <strong></strong>
                        </p>
                        <p></p>
                        <p>
                            <strong></strong>
                        </p>
                        <p>6.1 </p>
                        <p>6.2 </p>
                        <p>6.3 </p>
                    </div>
                </div>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false"> </el-button>
            </span>
        </el-dialog>
        <div class="data">
            <el-card class="box-card" style="background:rgba(255,255,255,0.9)">
                <el-row :gutte="50">
                    <el-col :span="5" style="padding:2rem">
                        <el-steps :active="active" :space="165" direction="vertical" finish-status="success">
                            <el-step title=""></el-step>
                            <el-step title=""></el-step>
                            <el-step title=""></el-step>
                        </el-steps>
                    </el-col>
                    <el-col :span="19" v-if="active==0">
                        <el-card class="box-card" style="padding:2.5rem">
                            <el-form :model="registerForm" :rules="rules" ref="registerForm" label-width="100px" class="demo-registerForm">
                                <el-form-item label="" prop="phone" style="margin:1.5rem;width:90%">
                                    <el-input v-model="registerForm.phone" style="width:90%"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="pass" style="margin:1.5rem;width:90%">
                                    <el-input type="password" v-model="registerForm.pass" style="width:90%"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="checkpass" style="margin:1.5rem;width:90%">
                                    <el-input type="password" v-model="registerForm.checkpass" style="width:90%"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="shape" style="margin:1.5rem;width:90%;padding:3px">
                                    <el-input v-model="registerForm.shape" style="width:35%" :disabled="shapeDisabled"></el-input>
                                    <span @click="imagedata=makeImage()" style="cursor:pointer">
                                        <img :src="imagedata" style=";vertical-align:middle;">
                                        <span @click="imagedata=makeImage()" style="color:#00CACA">?</span>
                                    </span>
                                </el-form-item>
                                <el-form-item label="" prop="phonemsg" style="margin:1.5rem;width:90%" v-if="isMsg">
                                    <el-input v-model="registerForm.phonemsg" style="width:35%"></el-input>
                                    <el-button @click="getMsg()" :disabled="msgDisabled">{{msgText}}</el-button>
                                </el-form-item>
                                <el-form-item prop="ischecked" style="margin-left:1.5rem">
                                    <el-checkbox v-model="registerForm.ischecked" @change="submitServer()"> </el-checkbox>
                                    <el-button type="text" @click="dialogVisible = true"></el-button>
                                </el-form-item>
                                <el-form-item style="margin:1.5rem;width:90%">
                                    <el-button type="primary" @click="submitForm('registerForm')"></el-button>
                                    <el-button @click="resetForm('registerForm')"></el-button>
                                </el-form-item>
                            </el-form>
                        </el-card>
                    </el-col>
                    <el-col :span="19" v-if="active==1">
                        <el-card class="box-card" style="padding:2.5rem">
                            <el-form :model="infoForm" :rules="rules" ref="infoForm" label-width="100px" class="demo-infoForm">
                                <el-form-item label="" prop="uname" style="margin:2rem;width:90%">
                                    <el-input v-model="infoForm.uname" style="width:90%"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="school" style="margin:2rem;width:90%">
                                    <el-input v-model="infoForm.school" style="width:90%"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="schooltell" style="margin:2rem;width:90%">
                                    <el-input v-model="infoForm.schooltell" style="width:90%"></el-input>
                                </el-form-item>
                                <el-form-item label="" prop="adress" style="margin:2rem;width:90%">
                                    <el-input v-model="infoForm.adress" style="width:90%"></el-input>
                                </el-form-item>
                                <el-form-item style="margin:2rem;width:90%">
                                    <el-button type="primary" @click="submitForm('infoForm')"></el-button>
                                    <el-button @click="resetForm('infoForm')"></el-button>
                                </el-form-item>
                            </el-form>
                        </el-card>
                    </el-col>
                    <el-col :span="19" v-if="active==2">
                        <el-card class="box-card" style="padding:2.5rem">
                            <el-form :model="infoForm" label-width="100px" class="demo-infoForm">
                                <h3 style="margin-left:30%">!</h3>
                                <el-form-item label="" style="margin:1.5rem;width:90%">
                                    {{infoForm.uname}}
                                </el-form-item>
                                <el-form-item label="" style="margin:1.5rem;width:90%">
                                    {{registerForm.phone}}
                                </el-form-item>
                                <el-form-item label="" prop="school" style="margin:1.5rem;width:90%">
                                    {{infoForm.school}}
                                </el-form-item>
                                <el-form-item label="" prop="schooltell" style="margin:1.5rem;width:90%">
                                    {{infoForm.schooltell}}
                                </el-form-item>
                                <el-form-item label="" prop="adress" style="margin:1.5rem;width:90%">
                                    {{infoForm.school}}
                                </el-form-item>
                                <el-form-item style="margin:1.5rem;width:90%">
                                    <el-button type="primary" @click="handleclick"></el-button>
                                </el-form-item>
                            </el-form>
                        </el-card>
                    </el-col>
                </el-row>
            </el-card>
        </div>
    </div>
</template>
<style>
.data {
    padding: 10em 10em;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    margin: auto;
}

.regcontent {
    padding: 2em 1em;
}
</style>
<script>
export default {
    data() {
        var checkphone = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                let Regphone = /(^(\+86|0086)?\s*1[34578]\d{9}$)/;
                if (!Regphone.test(value)) {
                    callback(new Error('11'));
                } else {
                    callback();
                }
            }
        };
        var checkPass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                let RegPass = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,21}$/;
                if (!RegPass.test(value)) {
                    callback(new Error('6-21!'));
                } else {
                    callback();
                }
            }
        };
        var checkshape = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                if (value != this.validdata) {
                    callback(new Error(''));
                } else {
                    this.isMsg = true
                    this.shapeDisabled = true
                    callback();
                }
            }
        };
        var checkserver = (rule, value, callback) => {
            if (value == false) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.registerForm.pass) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        var checkschooltell = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                let Regschooltell = /^((\d{3,4}\-)|)\d{7,8}(|([-\u8f6c]{1}\d{1,5}))$/;
                if (!Regschooltell.test(value)) {
                    callback(new Error('-!'));
                } else {
                    callback();
                }
            }
        };
        return {
            active: 0,
            imagedata: '',
            validdata: '',
            isMsg: false,
            msgDisabled: false,
            shapeDisabled: false,
            dialogVisible: false,
            msgText: '',
            registerForm: {
                phone: '',
                pass: '',
                checkpass: '',
                shape: '',
                phonemsg: '',
                ischecked: false,
            },
            infoForm: {
                uname: '',
                school: '',
                schooltell: '',
                adress: ''
            },
            rules: {
                phone: [
                    { required: true, validator: checkphone, trigger: 'change' },
                ],
                pass: [
                    { required: true, validator: checkPass, trigger: 'change' },
                ],
                checkpass: [
                    { required: true, validator: validatePass, trigger: 'change' }
                ],
                shape: [
                    { required: true, validator: checkshape, trigger: 'change' }
                ],
                phonemsg: [
                    { required: true, message: '', trigger: 'change' }
                ],
                uname: [
                    { required: true, message: '', trigger: 'change' },
                    { min: 2, max: 21, message: '', trigger: 'change' }
                ],
                school: [
                    { required: true, message: '', trigger: 'change' },
                    { min: 2, max: 21, message: '', trigger: 'change' }
                ],
                schooltell: [
                    { required: true, validator: checkschooltell, trigger: 'change' }
                ],
                adress: [
                    { required: true, message: '', trigger: 'change' },
                    { min: 5, max: 50, message: '', trigger: 'change' }
                ],
                ischecked: [
                    { validator: checkserver, trigger: 'change' }
                ]
            }
        }
    },
    mounted() {
        this.imagedata = this.makeImage()
    },
    methods: {
        makeImage() {
            let codes = [];
            for (let i = 48; i < 57; codes.push(i), i++);
            for (let i = 60; i < 90; codes.push(i), i++);
            for (let i = 97; i < 122; codes.push(i), i++);
            let arr = [];
            for (let i = 0; i < 4; i++) {
                let index = Math.floor(Math.random() * (61 - 0 + 1) + 0);
                let char = String.fromCharCode(codes[index]);
                arr.push(char);
            }
            this.validdata = arr.join("")
            let code = arr.join(" ");
            console.log(code)
            console.log(this.validdata)
            var canvas = document.createElement('canvas')
            canvas.style.marginTop = '5px'
            canvas.width = 70
            canvas.height = 25
            var ctx = canvas.getContext('2d')
            ctx.fillStyle = '#5bc0de'
            ctx.fillRect(10, 0, canvas.width, canvas.height)
            ctx.fillStyle = 'white'
            ctx.font = '14px arial'
            ctx.textAlign = 'center'
            ctx.fillText(code, 40, 20)
            var dataurl = canvas.toDataURL('image/png')
            return dataurl
        },
        submitServer() {
            this.$refs.registerForm.validateField('ischecked');
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log(this.registerForm.ischecked)
                    if (formName == 'registerForm') {
                        this.active = 1
                    } else if (formName == 'infoForm') {
                        this.active = 2
                    } else {
                        this.active = 3
                    }
                    console.log('succ')
                } else {
                    console.log('error submit!!');
                    console.log(this.registerForm.ischecked)
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        handleclick() {
            window.location.href = 'http://127.0.0.1:8010'
        },
        handleClose(done) {
            this.$confirm('')
                .then(_ => {
                    done();
                })
                .catch(_ => { });
        },
        getMsg() {
            this.msgDisabled = true
            let count = 60
            let msg = setInterval(() => {
                count--
                this.msgText = count + 's'
                console.log(this.msgText)
                if (count <= 0) {
                    clearInterval(msg)
                    this.msgDisabled = false
                    this.msgText = ''
                }
            }, 1000)
        }
    }

}
</script>


